import { ProjectModel } from './../../../classes/project.model';
import { ProjectService } from './../../../services/project.service';
import { BoardTaskModel } from './../../../classes/boardTask.model';
import { HashTagService } from './../../../services/hash-tag.service';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { CommentModel } from './../../../classes/comment.model';
import { CommentService } from './../../../services/comment.service';
import { TaskModel } from '../../../classes/task.model';
import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';

@Component({
  selector: 'app-task',
  templateUrl: './task.component.html',
  styleUrls: ['./task.component.scss'],
})

//     .
export class TaskComponent implements OnInit {
  //   .
  @Input() task: TaskModel;
  //  ,   .
  @Output() close = new EventEmitter();
  //  ,  .
  @Output() tasksByHashTag = new EventEmitter<BoardTaskModel[]>();

  //  .
  project: string;
  // .
  comments: CommentModel[];
  //  -  .
  showComments: boolean;
  //   / .
  commentButtonText: string = 'Show comments';
  //   .
  newCommmentForm: FormGroup;
  //      .
  showCommentForm: boolean;

  constructor(
    private projectService: ProjectService,
    private commentService: CommentService,
    private hashTagService: HashTagService,
    private formBuilder: FormBuilder
  ) {}

  //  .
  ngOnInit(): void {
    this.showCommentForm = false;
    this.showComments = false;
    this.project = this.task.ProjectId ? ` : ${this.task.ProjectId}` : '';
    this.commentFormInit();

    this.getProject();
  }

  //  .
  getProject() {
    this.projectService
      .getProject(this.task.ProjectId)
      .subscribe((data: ProjectModel) => {
        if (data) {
          this.project = `:${data.Title}`;
        }
      });
  }

  //    .
  commentFormInit() {
    this.newCommmentForm = this.formBuilder.group({
      content: [
        '',
        [
          Validators.required,
          Validators.minLength(6),
          Validators.maxLength(400),
        ],
      ],
    });
  }

  //   .
  submitNewComment() {
    const content = this.newCommmentForm.controls.content;
    if (content.valid) {
      const username = localStorage.getItem('username');

      const commentModel = new CommentModel();
      commentModel.TaskId = this.task.TaskId;
      commentModel.Login = username;
      commentModel.Content = content.value;

      this.commentService.addComment(commentModel).subscribe(() => {
        this.showCommentForm = false;
        this.getComments();
      });
    }
  }

  //    / .
  onClick() {
    if (!this.showComments) {
      this.getComments();
    }

    this.showComments = !this.showComments;
    this.commentButtonText = this.showComments
      ? 'Hide comments'
      : 'Show comments';
  }

  //    .
  getComments() {
    this.commentService
      .getTaskComments(this.task.TaskId)
      .subscribe((data: CommentModel[]) => {
        if (data) {
          this.comments = data;
        }
      });
  }

  //   -    .
  sendHashTag(event: any) {
    this.hashTagService
      .getTasks(event.target.innerText)
      .subscribe((data: BoardTaskModel[]) => {
        if (data) {
          this.tasksByHashTag.emit(data);
        }
      });
  }

  //     .
  closeTaskDetail() {
    this.close.emit();
  }

  //     New comment.
  newCommentClick() {
    if (!this.showCommentForm) {
      //    .
      this.newCommmentForm.controls.content.setValue('');
    }

    // /   .
    this.showCommentForm = !this.showCommentForm;
  }
}
