import { CommentService } from './../../services/comment.service';
import { CommentModel } from './../../classes/comment.model';
import {
  Component,
  Input,
  OnInit,
  EventEmitter,
  Output,
  ViewChild,
  ElementRef
} from '@angular/core';

@Component({
  selector: 'app-comment',
  templateUrl: './comment.component.html',
  styleUrls: ['./comment.component.scss'],
})

//   .
export class CommentComponent implements OnInit {
  //   - .
  @Input() comment: CommentModel;
  //   -  .
  @Output() updateComments = new EventEmitter();

  @ViewChild('textArea', { read: ElementRef }) textArea: ElementRef;

  //  -   edit  delete.
  showControls: boolean;
  //  -   send.
  showSend: boolean;
  //  -   .
  readonly: boolean;
  //  -   .
  editContent: boolean;

  constructor(private commentService: CommentService) {}

  ngOnInit(): void {
    this.readonly = true;
    const userName = localStorage.getItem('username');
    this.showControls = userName === this.comment.Login;
  }

  //  .
  editComment() {
    //  DOM  textarea   .   .
    const taxtAreaElement = this.textArea;

    this.readonly = false;
    this.showSend = true;
    this.editContent = true;
  }

  saveComment() {
    this.commentService.updateComment(this.comment).subscribe(() => {
      //  -  .
      this.updateComments.emit();
    });
  }

  //  .
  deleteComment() {
    this.commentService
      .deleteComment(this.comment.CommentId)
      .subscribe((data) => {
        //  -  .
        this.updateComments.emit();
      });
  }
}
