import { TaskManagerService } from 'src/app/services/task-manager.service';
import { BoardTaskModel } from './../../../../classes/boardTask.model';
import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';

@Component({
  selector: 'app-tasks-executor',
  templateUrl: './tasks-executor.component.html',
  styleUrls: ['./tasks-executor.component.scss']
})

//    ,     .
export class TasksExecutorComponent implements OnInit {

  //   - ,  .
  @Input() tasks: BoardTaskModel[] = [];
  //  ,  .
  @Output() taskIdEmitter = new EventEmitter();
  //  ,  .
  @Output() updateTaskEmitter = new EventEmitter();
  //  .
  @Output() delete = new EventEmitter<number>();

  //  .
  userName: string;

  constructor() { }

  //  .
  ngOnInit(): void {
  }

  //  id       .
  getTaskById(taskId: number) {
    this.taskIdEmitter.emit(taskId);
  }

  //  id       .
  updateTask(taskId: number) {
    this.updateTaskEmitter.emit(taskId);
  }

  //  .
  deleteTask(taskId: number) {
    this.delete.emit(taskId);
  }
}
