import { PersonalComponent } from './components/main/personal/personal.component';
import { BoardComponent } from './components/main/board/_board/board.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { MainComponent } from './components/main/_main/main.component';
import { UserComponent } from './components/main/user/user.component';
import { SignUpComponent } from './components/main/user/sign-up/sign-up.component';
import { SignInComponent } from './components/main/user/sign-in/sign-in.component';
import { AuthGuard } from './authGuard/auth.guard';

//  .
const routes: Routes = [
  {
    path: 'sign-up',
    component: UserComponent,
    children: [{ path: '', component: SignUpComponent }],
  },
  {
    path: 'sign-in',
    component: UserComponent,
    children: [{ path: '', component: SignInComponent }],
  },
  { path: '', redirectTo: '/sign-in', pathMatch: 'full' },
  {
    path: 'main',
    component: MainComponent,
    canActivate: [AuthGuard],
    children: [
      { path: 'board', component: BoardComponent },
      { path: 'personal', component: PersonalComponent },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule],
})

// .
export class AppRoutingModule {}
