import { Component, OnInit } from '@angular/core';
import { AccountModel } from 'src/app/classes/account.model';
import { UserModel } from '../../../../classes/user.model';
import { UserService } from '../../../../services/user.service';

@Component({
  selector: 'app-sign-up',
  templateUrl: './sign-up.component.html',
  styleUrls: ['./sign-up.component.scss'],
})

//   .
export class SignUpComponent implements OnInit {
  // .
  user: UserModel = new UserModel();
  // 
  account: AccountModel = new AccountModel();
  //   .
  buttonName: string;
  //  .
  confirmPassword: string;

  constructor(private userService: UserService) {}

  //  .
  ngOnInit(): void {
    this.resetForm();
    this.buttonName = 'Sign ';
  }

  //          .
  checkFields(): boolean {
    if (
      this.account.UserName !== '' &&
      this.account.Password !== '' &&
      this.confirmPassword !== '' &&
      // this.user.FirstName !== '' &&
      // this.user.LastName !== '' &&
      this.account.Password === this.confirmPassword
    ) {
      return true;
    }

    return false;
  }

  //  .
  resetForm(): void {
    this.account.UserName = '';
    this.account.Password = '';
    this.user.FirstName = '';
    this.user.LastName = '';
    this.confirmPassword = '';
  }

  //   .
  onSubmit(): void {
    if (this.checkFields()) {
      this.buttonName = 'Sending Data';
      this.userService.registerUser(this.account).subscribe(
        (data) => {
          //     
          this.resetForm();
          alert("Registration success!");
        },
        //     
        (error) => {
          this.buttonName = 'Error';
        }
      );
    } else {
      this.buttonName = 'Error';
    }
  }
}
