import { CommentModel } from './../classes/comment.model';
import { Injectable } from '@angular/core';
import { HttpClient, HttpResponse, HttpHeaders } from '@angular/common/http';

@Injectable({
  providedIn: 'root',
})
export class CommentService {
  private rootUrl = 'https://localhost:44376';
  httpOptions = {
    headers: new HttpHeaders(),
  };

  constructor(private http: HttpClient) {}

  //      .
  getTaskComments(taskId: number) {
    this.httpOptions.headers = new HttpHeaders();
    const route = '/comment/GetCommentsTask';
    return this.http.get(
      `${this.rootUrl}${route}?taskid=${taskId}`,
      this.httpOptions
    );
  }

  //    .
  addComment(commentModel: CommentModel) {
    const route = '/comment/AddComment';
    const body = commentModel;
    this.httpOptions.headers = new HttpHeaders();
    return this.http.post(`${this.rootUrl}${route}`, body, this.httpOptions);
  }

  //    .
  deleteComment(commentId: number) {
    const route = '/comment/DeleteComment';
    this.httpOptions.headers = new HttpHeaders();
    return this.http.delete(
      `${this.rootUrl}${route}?commentId=${commentId}`,
      this.httpOptions
    );
  }

  //    .
  updateComment(comment: CommentModel) {
    const route = '/comment/ChangeComment';
    const body = comment;
    this.httpOptions.headers = new HttpHeaders();
    return this.http.put(`${this.rootUrl}${route}`, body, this.httpOptions);
  }
}
