import { HttpHeaders, HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root',
})

//      -.
export class HashTagService {
  readonly rootUrl = 'https://localhost:44376';
  httpOptions = {
    headers: new HttpHeaders(),
  };

  constructor(private http: HttpClient) {}

  //       (,   #).
  parseHashTags(text: string): string[] {
    const regExp = /#[0-9a-zA-Z--]+\b/gu;
    let hashTags = text.match(regExp)?.map(s => s.slice(1));
    return hashTags;
  }

  //       -.
  getTasks(hashTag: string): Observable<any> {
    const route = '/HashTag/getTasks';
    return this.http.get(`${this.rootUrl}${route}?hashTag=${hashTag}`);
  }

  //   post       
  addHashTags(hashTags: string[], taskId: number): Observable<any> {
    const route = '/HashTag/AddHashTags';
    const body = hashTags;
    return this.http.post(`${this.rootUrl}${route}?taskId=${taskId}`, body);
  }
}
