import { SharedEventService } from './../../../../services/shared-event.service';
import { AccountModel } from './../../../../classes/account.model';
import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import { UserService } from '../../../../services/user.service';
import { HttpErrorResponse } from '@angular/common/http';
import { Router } from '@angular/router';

@Component({
  selector: 'app-sign-in',
  templateUrl: './sign-in.component.html',
  styleUrls: ['./sign-in.component.scss'],
})

//  .
export class SignInComponent implements OnInit {
  //  ,  .
  @Output() loginEmitter: EventEmitter<boolean> = new EventEmitter();
  //  -   .
  isLoginError: boolean = false;
  // .
  accountModel: AccountModel = new AccountModel();

  constructor(
    private userService: UserService,
    private router: Router,
    private sharedEventService: SharedEventService
  ) {}

  //  .
  ngOnInit(): void {}

  //   .
  onSubmit() {
    this.userService.userAuthentication(this.accountModel).subscribe(
      (data: any) => {
        localStorage.setItem('token', data.access_token);
        localStorage.setItem('username', this.accountModel.UserName);
        this.router.navigate(['/main']);

        //      .
        this.sharedEventService.emitSignIn(true);
        this.sharedEventService.getUserName(this.accountModel.UserName);
      },
      (err: HttpErrorResponse) => {
        this.isLoginError = true;
      }
    );
  }
}
