import { TaskManagerService } from 'src/app/services/task-manager.service';
import { BoardTaskModel } from './../../../classes/boardTask.model';
import { Component, Input, OnInit, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-user-task',
  templateUrl: './user-task.component.html',
  styleUrls: ['./user-task.component.scss'],
})

//       .
export class UserTaskComponent implements OnInit {
  //  , .
  @Input() task: BoardTaskModel = new BoardTaskModel();

  //  ,  .
  @Output() taskIdEmitter = new EventEmitter();
  //  ,  .
  @Output() updateTaskEmitter = new EventEmitter();
  //  ,  .
  @Output() delete = new EventEmitter<number>();

  //      .
  author: boolean;

  constructor(private taskManagerService: TaskManagerService) {}

  //  .
  ngOnInit(): void {
    const userName = localStorage.getItem('username');
    this.author = userName === this.task.AuthorLogin;
  }

  //      .    id .
  onClickUserTask() {
    this.taskIdEmitter.emit(this.task.TaskId);
  }

  //  .
  deleteTask() {
    this.delete.emit(this.task.TaskId);
  }

  //  .     id .
  updateTask() {
    this.updateTaskEmitter.emit(this.task.TaskId);
  }
}
