import { Status } from './../../../enums/status.enums';
import { HashTagService } from './../../../services/hash-tag.service';
import { MatDatepicker } from '@angular/material/datepicker';
import { MatInputModule } from '@angular/material/input';
import { UserModel } from './../../../classes/user.model';
import { ProjectModel } from './../../../classes/project.model';
import { ProjectService } from './../../../services/project.service';
import { UserService } from './../../../services/user.service';
import { TaskManagerService } from 'src/app/services/task-manager.service';
import { TaskModel } from './../../../classes/task.model';
import { Priority } from './../../../enums/priority.enum';
import { Component, Input, OnInit, Output, EventEmitter } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { from } from 'rxjs';

@Component({
  selector: 'app-task-form',
  templateUrl: './task-form.component.html',
  styleUrls: ['./task-form.component.scss'],
})

//  -     .
export class TaskFormComponent implements OnInit {
  //  - id      .
  @Input() parenTaskId: string;
  //  -   .
  @Input() changeTask: TaskModel;

  //   -  .
  @Output() close = new EventEmitter();
  //   -  .
  @Output() send = new EventEmitter();

  //  .
  taskFormGroup: FormGroup;
  //     Priority.
  priority = Priority;
  //     Status.
  status = Status;
  //       .
  task: TaskModel = new TaskModel();
  //  .
  projects: ProjectModel[];
  //  .
  project: ProjectModel = new ProjectModel();
  //   .
  users: string[];
  //   .
  priorityText: string;
  //   .
  statusText: string;
  //  -  .
  hideProjectMenu: boolean;
  //  -  .
  hideUserMenu: boolean;
  //  -   .
  statusFlag: boolean;

  constructor(
    private formBuilder: FormBuilder,
    private taskManager: TaskManagerService,
    private userService: UserService,
    private projectService: ProjectService,
    private hashTagService: HashTagService
  ) {}

  //  .
  ngOnInit(): void {
    this.hideProjectMenu = true;
    this.hideUserMenu = true;
    this.priorityText = this.task.Priority;
    this.statusText = this.task.Status;
    this.statusFlag = this.changeTask?.TaskId ? true : false;
    this.initForm();
  }

  //  .
  initForm() {
    this.taskFormGroup = this.formBuilder.group({
      title: [
        '',
        [
          Validators.required,
          Validators.pattern('[a-zA-Z--0-9 ]+'),
          Validators.minLength(3),
          Validators.maxLength(20),
        ],
      ],
      description: [
        '',
        [
          Validators.required,
          Validators.minLength(6),
          Validators.maxLength(200),
        ],
      ],
      executor: ['', []],
      project: ['', []],
      deadlineDate: ['', [Validators.required]],
      priority: ['', []],
      status: ['', []],
    });

    const controls = this.taskFormGroup.controls;

    if (this.changeTask.TaskId) {
      this.priorityText = this.changeTask.Priority;
      controls.title.setValue(this.changeTask.Title);
      controls.description.setValue(this.changeTask.Description);
      controls.executor.setValue(this.changeTask.ExecutorLogin);
      controls.priority.setValue(this.changeTask.Priority);
      controls.deadlineDate.setValue(this.changeTask.DeadlineDate);
    }
  }

  //    .
  onSubmit() {
    const controls = this.taskFormGroup.controls;
    const valid = this.isValid();

    if (!valid) {
      return;
    }

    this.task.TaskId = this.changeTask.TaskId;
    this.task.AuthorLogin = localStorage.getItem('username');
    this.task.Title = controls.title.value;
    this.task.Description = controls.description.value;
    this.task.DeadlineDate = controls.deadlineDate.value;
    this.task.ExecutorLogin = controls.executor.value || this.task.AuthorLogin;
    this.task.Priority = this.priorityText;
    this.task.ProjectId = this.project.ProjectId;
    this.task.Status = this.changeTask?.TaskId
      ? this.statusText
      : this.task.Status;

    //   -    .
    this.task.HashTags = this.hashTagService.parseHashTags(
      this.task.Description
    );

    if (this.changeTask.TaskId) {
      this.taskManager.changeTask(this.task).subscribe(() => {
        console.log('change task');
        //     .
        this.send.emit();
      });
    } else {
      this.taskManager.addTask(this.task).subscribe(() => {
        console.log('add task');
        //     .
        this.send.emit();
      });
    }
  }

  //   .
  isValid(): boolean {
    const controls = this.taskFormGroup.controls;
    return controls.title.valid && controls.description.valid;
  }

  //      .
  pickPriority(p: string) {
    this.priorityText = p;
  }

  //      .
  pickStatus(s: string) {
    this.statusText = s;
  }

  //   .
  getUsers() {
    this.hideUserMenu = false;
    this.userService.getUsers().subscribe((data: []) => {
      if (data) {
        this.users = data;
      }
    });
  }

  //  .
  setUser(user: string) {
    const userInput = this.taskFormGroup.controls.executor;
    userInput.setValue(user);

    this.hideUserMenu = true;
  }

  //   
  getProjects() {
    this.hideProjectMenu = false;
    //       .
    if (!this.projects) {
      this.projectService.getAllProjects().subscribe(
        (data: ProjectModel[]) => {
          if (data) {
            this.projects = data;
          }
        },
        (error) => {
          console.log('error get all projects');
        }
      );
    }
  }

  //  .
  setProject(project: ProjectModel) {
    const projectInput = this.taskFormGroup.controls.project;
    projectInput.setValue(project.Title);

    this.project = project;
    this.hideProjectMenu = true;
  }

  //  none   .
  setNoneProject() {
    this.hideProjectMenu = true;
    this.taskFormGroup.controls.project.setValue(null);
  }

  //  .
  closeForm() {
    this.close.emit();
  }
}
