import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { BoardTaskModel } from 'src/app/classes/boardTask.model';

@Component({
  selector: 'app-board-task',
  templateUrl: './boardTask.component.html',
  styleUrls: ['./boardTask.component.scss'],
})

//  -     .
export class BoardTaskComponent implements OnInit {
  //     .
  @Input() boardTask: BoardTaskModel;

  //   -  .
  @Output() taskIdEmitter = new EventEmitter();
  //  .
  priorityClass: string;
  //  .
  author: string;

  constructor() {}

  //  .
  ngOnInit(): void {
    if (this.boardTask) {
      const priority = this.boardTask.Priority.toLowerCase();
      this.priorityClass = `task-${priority}`;
      this.author = this.boardTask.AuthorLogin.substr(0, 2);
    }
  }

  //      .     id .
  boardTaskOnClick() {
    this.taskIdEmitter.emit(this.boardTask.TaskId);
  }
}
