import { SharedEventService } from './../../../services/shared-event.service';
import { Router } from '@angular/router';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-main-menu',
  templateUrl: './main-menu.component.html',
  styleUrls: ['./main-menu.component.scss'],
})

//   .
export class MainMenuComponent implements OnInit {

  //  -  .
  isLogged: boolean = false;

  constructor(
    private router: Router,
    private sharedEventService: SharedEventService
  ) {
    //     Sign-in.
    sharedEventService.changeEmitted$.subscribe((logged) =>
      this.signIn(logged)
    );
  }

  //  .
  ngOnInit(): void {
    this.isLogged = localStorage.getItem('token') ? true : false;
  }

  //   .
  toBoard() {
    this.router.navigate(['main/board']);
  }

  //   .
  toPersonal() {
    this.router.navigate(['main/personal']);
  }

  //    .
  signOut() {
    this.isLogged = false;
    localStorage.clear();
    this.router.navigate(['sign-in']);
    this.sharedEventService.getUserName('');
  }

  //    .
  signIn(logged: boolean) {
    this.isLogged = logged;
  }
}
