import { Component, OnInit } from '@angular/core';
import { BoardTaskModel } from 'src/app/classes/boardTask.model';
import { TaskManagerService } from 'src/app/services/task-manager.service';
import { TaskModel } from 'src/app/classes/task.model';

@Component({
  selector: 'app-board',
  templateUrl: './board.component.html',
  styleUrls: ['./board.component.scss'],
})

//  -    .
export class BoardComponent implements OnInit {
  constructor(private taskManagerService: TaskManagerService) {}

  //   .
  boardTasks: BoardTaskModel[] = [];
  //  -    .
  showTaskDetail: boolean;
  //    .
  task: TaskModel;
  //  - / 
  loading: boolean;

  //  .
  ngOnInit(): void {
    this.showTaskDetail = false;
    this.loading = true;

    //     .
    this.taskManagerService
      .getBoardTasks()
      .subscribe((data: BoardTaskModel[]) => {
        if (data) {
          this.boardTasks = data;
          this.loading = false;
        }
      }),
      (error: any) => {
        alert('Something wrong!');
        this.loading = false;
      };
  }

  //     id.
  getTaskById(taskId: number) {
    this.taskManagerService.getTask(taskId).subscribe((data: TaskModel) => {
      if (data) {
        this.task = data;
        this.showTaskDetail = true;
      }
    });
  }

  //   .
  closeTaskDetail() {
    this.showTaskDetail = false;
  }
}
