import { ProjectModel } from './../../../classes/project.model';
import { ProjectService } from './../../../services/project.service';
import { TaskModel } from './../../../classes/task.model';
import { TaskManagerService } from './../../../services/task-manager.service';
import { BoardTaskModel } from './../../../classes/boardTask.model';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';

@Component({
  selector: 'app-personal',
  templateUrl: './personal.component.html',
  styleUrls: ['./personal.component.scss'],
})

//   .
export class PersonalComponent implements OnInit {
  constructor(
    private taskManagerService: TaskManagerService,
    private formBuilder: FormBuilder,
    private projectService: ProjectService
  ) {}

  //   .
  userName: string;
  //   .
  projectFormGroup: FormGroup;
  //    .
  project: ProjectModel;
  //   .
  projects: ProjectModel[] = [];
  //   .
  assignedTasks: BoardTaskModel[] = [];
  //   .
  userTasks: BoardTaskModel[] = [];
  //    .
  task: TaskModel;
  //  -   .
  showTaskForm: boolean;
  //  -   .
  showTaskDetail: boolean;
  //  -   .
  showInput: boolean;
  //     /  .
  myTasks: boolean;
  //     .
  taskComponentHeader: string;
  //    toggle.
  toggleTitleText: string;

  //  .
  ngOnInit(): void {
    this.toggleTitleText = 'To my tasks';
    this.taskComponentHeader = 'Tasks assigned to me';
    this.initForm();

    this.showTaskForm = false;
    this.showTaskDetail = false;
    this.showInput = false;

    this.project = new ProjectModel();

    //  .
    this.userName = localStorage.getItem('username');
    this.getProjects();
  }

  //  .
  initForm() {
    this.projectFormGroup = this.formBuilder.group({
      title: [
        '',
        [
          Validators.required,
          Validators.pattern('[a-zA-Z--0-9_]+'),
          Validators.minLength(3),
          Validators.maxLength(40),
        ],
      ],
    });
  }

  //     id.
  getTaskById(taskId: number) {
    this.taskManagerService.getTask(taskId).subscribe((data: TaskModel) => {
      if (data) {
        //  .
        this.showTaskDetail = true;
        this.task = data;
      }
    });
  }

  //      .
  getProjects() {
    this.projectService.getAccountProjects(this.userName).subscribe(
      (data: ProjectModel[]) => {
        if (data) {
          this.projects = data;
          this.getAssignedTasks();
        }
      },
      (error) => {
        console.log('error');
      }
    );
  }

  //      .
  getTasks() {
    this.taskManagerService
      .getUserTasks(this.userName)
      .subscribe((data: BoardTaskModel[]) => {
        if (data) {
          this.userTasks = data;
        }
      });
  }

  //    .
  getAssignedTasks() {
    this.taskManagerService
      .GetAssignedTasks(this.userName)
      .subscribe((data: BoardTaskModel[]) => {
        if (data) {
          this.assignedTasks = data;
        }
      });
  }

  //  .
  updateTask(taskId: number) {
    this.taskManagerService.getTask(taskId).subscribe((data: TaskModel) => {
      if (data) {
        this.task = data;
        this.showTaskForm = true;
      }
    });
  }

  //  .
  createTask() {
    this.task = new TaskModel();
    this.showTaskForm = true;
  }

  //   .
  closeTaskForm() {
    this.showTaskForm = false;
  }

  //      .
  sendTask() {
    this.closeTaskForm();
    if (this.myTasks) {
      this.getTasks();
    }
    else {
      this.getAssignedTasks();
    }
  }

  //  .
  deleteTask(taskId: number) {
    console.log(taskId);
    this.taskManagerService
    .deleteTask(taskId)
    .subscribe(() => {
      if (this.myTasks) {
        this.getTasks();
      }
      else {
        this.getAssignedTasks();
      }
    });
  }

  //  .
  deleteProject(projectId: number) {
    this.projectService.deleteProject(projectId).subscribe((data:boolean) => {
      if (data) {
        this.getProjects();
      }
    },
    (error) => {
      console.log("Delete project error");
    }
    )
  }

  //   .
  closeTaskDetail() {
    this.showTaskDetail = false;
  }

  showTaskByHashTag(tasks: BoardTaskModel[]) {
    this.closeTaskDetail();
  }

  //  .
  createProject() {
    this.showInput = !this.showInput;
  }

  //   .
  check(checked: boolean) {
    this.myTasks = checked;
    this.taskComponentHeader = this.myTasks
      ? 'My tasks'
      : 'Tasks assigned to me';

    this.toggleTitleText = this.myTasks ? 'To assigned tasks' : 'To my tasks';
    if (this.myTasks) {
      this.getTasks();
    }
    else {
      this.getAssignedTasks();
    }
  }

  //   .
  onSubmit() {
    this.showInput = false;
    const title = this.projectFormGroup.controls.title;

    this.project.AuthorId = this.userName;
    this.project.Title = title.value;
    title.setValue('');

    this.projectService.addProject(this.project).subscribe((data) => {
      if (data) {
        this.getProjects();
      }
    });
  }
}
