import { SharedEventService } from './../../../services/shared-event.service';
import { AppDataStorageService } from 'src/app/services/app-data-storage.service';
import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-user',
  templateUrl: './user.component.html',
  styleUrls: ['./user.component.scss'],
})

//      .
export class UserComponent implements OnInit {
  //   - .
  @Output() signInEmitter: EventEmitter<boolean> = new EventEmitter();
  //   - .
  @Output() signUpEmitter: EventEmitter<boolean> = new EventEmitter();

  constructor(private activatedRoute: ActivatedRoute) {}

  //  .
  ngOnInit(): void {}

  // .
  SignIn(logged: boolean) {
    console.log('user');
  }

  // .
  emitSignUp(event: boolean) {
    this.signUpEmitter.emit(event);
  }
}
